import { newSpecPage } from '@stencil/core/testing';
import { PrestoBunny } from '../presto-bunny';

describe('presto-bunny', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoBunny],
      html: `<presto-bunny></presto-bunny>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-bunny>
        <presto-video></presto-video>
      </presto-bunny>
    `);
  });
});
